import { useState, useEffect, ChangeEvent } from "react";
import { useTranslation } from 'react-i18next';
//import { Carousel } from 'react-bootstrap';
import "bootstrap/dist/css/bootstrap.min.css";
import { UncontrolledCarousel } from 'reactstrap';

const Home = () => {
  const { t, i18n } = useTranslation();

  return (
    <>
      <UncontrolledCarousel
        items={[
          {
            altText: 'Slide 1',
            caption: '',
            key: 1,
            src: process.env.PUBLIC_URL + '/images/forest_winter_trees.jpg'
          },
          {
            altText: 'Slide 2',
            caption: '',
            key: 2,
            src: process.env.PUBLIC_URL + '/images/train_railway_bridge.jpg'
          },
          {
            altText: 'Slide 3',
            caption: '',
            key: 3,
            src: process.env.PUBLIC_URL + '/images/winter_mountains_snow.jpg'
          }
        ]}
      />

      <div className="container">
        <div className="mt-3 list row">
          <div className="col-md-12">
            <h4>{t('home.title')}</h4>
          </div>
        </div>
        <div className="mt-3 list row">
          <div className="col-md-4 p-2">
            <div className="card">
              <img src={process.env.PUBLIC_URL + '/images/forest_winter_trees.jpg'} className="card-img-top" alt="" />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                <a href="#" className="btn btn-primary">Go somewhere</a>
              </div>
            </div>
          </div>
          
          <div className="col-md-4 p-2">
            <div className="card">
              <img src={process.env.PUBLIC_URL + '/images/train_railway_bridge.jpg'} className="card-img-top" alt="" />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                <a href="#" className="btn btn-primary">Go somewhere</a>
              </div>
            </div>
          </div>

          <div className="col-md-4 p-2">
            <div className="card">
              <img src={process.env.PUBLIC_URL + '/images/winter_mountains_snow.jpg'} className="card-img-top" alt="" />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                <a href="#" className="btn btn-primary">Go somewhere</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Home